@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);

.input {
    width: 90%;
    padding: 20px;
    margin-top: 40px;

}

@media (orientation: portrait) and (min-width: 768px) {
    .input {
        width: 80%;

    }
}

@media (orientation: landscape) and (min-width: 1024px) {
    .input {
        width: 60%;
    }
}
* {
    font-family: 'Comfortaa', cursive;
    color: whitesmoke;

}

.content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    font-size: 20px;
}

.box {
    display: flex;
    flex-direction: row;
    padding: 5x;
}

svg {
    margin: 4px;
}

.temp {
    font-size: 60px;
}

div.result {
    text-align: center;
    font-size: 26px;
}

@media (orientation: portrait) and (min-width: 360px) {
    .content {
        font-size: 30px;
        padding: 20px;
    }

    .temp {
        font-size: 80px;
    }

    .content div {
        padding: 5px;
    }
}

@media (orientation: portrait) and (min-width: 768px) {
    .content {
        font-size: 50px;
        padding: 20px;
    }

    .temp {
        font-size: 140px;
    }

    .content div {
        padding: 10px;
    }
}

@media (orientation: landscape) and (min-width: 1024px) {
    .content {
        font-size: 30px;
        padding: 10px;
    }

    .temp {
        font-size: 70px;
    }

    .content div {
        padding: 0px;
    }
}
.App {
    background-image: url(/ReactWeatherApp/static/media/bgc.efcca5cc.png);
    min-height: 100vh;
    min-width: 100vw;
    background-position: center;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;

}

.wrapper {
    flex-grow: 1;
    background-color: rgba(0, 0, 0, 0.2);
    width: 100%;
}

* {
    margin: 0 auto;
}
